  layer1.style.opacity = 2 - Math.abs(positionY - windowHeight * 3) / (windowHeight / 3);
  layer2.style.opacity = 2 - Math.abs(positionY - windowHeight * 6) / (windowHeight / 3);
  layer3.style.opacity = 2 - Math.abs(positionY - windowHeight * 9) / (windowHeight / 3);
  layer4.style.opacity = 2 - Math.abs(positionY - windowHeight * 12) / (windowHeight / 3);
